code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(7, 9, 27);
  text-align: center;
}


.Cell {
  height: 3em;
  width: 3em;
  background-color: rgb(191, 224, 255);
  border-radius: 3px;
 
}

.Cell-lit {
  background-color: rgb(0, 44, 65);
}
.Board {
  margin: 0 auto;
  box-shadow: 0px 0px 14px 0px rgb(38, 255, 237);
  border-radius: 4px;
}

.Rules{
  color: aliceblue;
  text-align: left;
  margin: 10px;
  position: absolute;
  bottom: 20px;
  border: 3px red solid;
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0px 0px 7px 0px rgba(255,38,38,1);
}

