.Board {
  margin: 0 auto;
  -webkit-box-shadow: 0px 0px 14px 0px rgb(38, 255, 237);
  -moz-box-shadow: 0px 0px 14px 0px rgb(38, 255, 237);
  box-shadow: 0px 0px 14px 0px rgb(38, 255, 237);
  border-radius: 4px;
}

.Rules{
  color: aliceblue;
  text-align: left;
  margin: 10px;
  position: absolute;
  bottom: 20px;
  border: 3px red solid;
  border-radius: 10px;
  padding: 5px;
  -webkit-box-shadow: 0px 0px 7px 0px rgba(255,38,38,1);
  -moz-box-shadow: 0px 0px 7px 0px rgba(255,38,38,1);
  box-shadow: 0px 0px 7px 0px rgba(255,38,38,1);
}

